import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { IRetailer } from 'src/mapitApiTypes';

export const retailersApi = createApi({
  reducerPath: 'retailersApi',
  baseQuery: fetchBaseQuery({ baseUrl: '/api' }),
  endpoints: (builder) => ({
    getRetailers: builder.query<{ count: number, items: IRetailer[] }, { baseQuery: string, center_id: string, limit: number, page?: number }>({
      query: ({ baseQuery, center_id, limit, page = 1 }) => ({
        url: `${baseQuery}/v1/retailers?limit=${limit}&page=${page}`,
        headers: {
          center_id,
        },
      }),
    })
  })
})

export const { useGetRetailersQuery, useLazyGetRetailersQuery } = retailersApi;