import { createContext, useContext, useState } from "react"
import { IConfig, IExtMesh } from "src/types";


export type TMeshObjectContext = {
  MeshObject: IExtMesh[] | null;
  SetMeshObject: React.Dispatch<React.SetStateAction<IExtMesh[] | null>> | Function;
  cameraLength: number | undefined,
  setCameraLength: React.Dispatch<React.SetStateAction<number | undefined>> | Function;
  config: IConfig | undefined,
  setConfig: React.Dispatch<React.SetStateAction<IConfig | undefined>> | Function;
}

const MeshObjectContext = createContext<TMeshObjectContext>({
  MeshObject: null,
  SetMeshObject: () => null,
  cameraLength: undefined,
  setCameraLength: () => null,
  config: undefined,
  setConfig: () => null,
});

export default function MeshObjectContextProvider({ children }: { children: React.ReactNode }) {
  const [MeshObject, SetMeshObject] = useState<IExtMesh[] | null>(null);
  const [config, setConfig] = useState<IConfig | undefined>();
  const [cameraLength, setCameraLength] = useState<number | undefined>();

  return (
    <MeshObjectContext.Provider
      value={{
        MeshObject,
        SetMeshObject,
        cameraLength,
        setCameraLength,
        config,
        setConfig
      }}
    >
      {children}
    </MeshObjectContext.Provider>
  )
}

export function useMeshObjectContext() {
  const context = useContext(MeshObjectContext);

  if (context === undefined) {
    throw new Error("useMeshObjectContext must be used within MeshObjectContextProvider");
  }

  return context;
}
