export const textLogoNamePrefix = 'custom-layer-';

const config: IJsonConfig = { ...defaultVars, ...mapit2DefaultVars }

const defaultMapObjValues = {
  id: null,
  center_id: config.CENTER_ID,
  retailer_id: null,
  kiosk_id: null,
  map_obj_name: '',
  obj_type: 'retailer',
  layer_type: 'retail_name',
  value: '',
  custom_text: '',
  custom_image: '',
  hover_text: '',
  bg_color: '',
  transparent: 0,
  text_color: '',
  size: 8,
  rotate: 0,
  offsetX: 0,
  offsetY: 0,
} as Record<string, string | number | null | undefined>;


let allNonIndexedMapObjects: MapObj[] = [];


// const floorsData:IFloorData[] = [];
let MEDIA_STORAGE_URI: string = '';


const clearObject = (obj: Record<string, MapObj>) => {
  for (let key in obj) {
    delete obj[key];
  }
}

function makeIndexedMapObjects(allIndexedMapObjects: Record<string, MapObj>, map_objs: MapObj[]) {
  clearObject(allIndexedMapObjects);

  allNonIndexedMapObjects = map_objs;

  for (let i = 0; i < map_objs.length; i++) {
    const obj = map_objs[i];
    allIndexedMapObjects[obj.map_obj_name] = obj;
    for (let key in defaultMapObjValues) {
      const value = defaultMapObjValues[key];
      //@ts-ignore
      if (value != null && obj[key] == null) {
        //@ts-ignore
        allIndexedMapObjects[obj.map_obj_name][key] = value;
      }
    }
  }
}

const init = (allIndexedMapObjects: Record<string, MapObj>, allIndexedRetailers: Record<string, IRetailer>, config: IJsonConfig, floors: IFloorData[], response: MapIt2Response) => {
  const KIOSKS: Record<string, Kiosk> = {};
  response.kiosks.forEach((kiosk) => {
    const mapObj = response.map_objs.find(item => item.kiosk_id === kiosk.id);
    if (mapObj) {
      kiosk.map_obj_name = mapObj.map_obj_name;
    }
    KIOSKS[kiosk.id] = kiosk;
  });


  for (let index in response.retailers) {
    const retailer = response.retailers[index];
    allIndexedRetailers[retailer.id] = retailer;
    allIndexedRetailers[retailer.id].index = index;
  }

  // ngCtrlElem.scope().amenitiesNav.bg_color = response.settings.AMENITIES_NAV_BG_COLOR;
  // ngCtrlElem.scope().amenitiesNav.icon_color = response.settings.AMENITIES_NAV_ICON_COLOR;

  makeIndexedMapObjects(allIndexedMapObjects, response.map_objs);

  floors.length = 0;
  response.floors.forEach((value: IFloorData, index: number) => {
    const objsGroup = new Group();
    objsGroup.scale.y *= -1;
    objsGroup.lookAt(0, 1, 0);

    floors.push({
      id: value.id,
      // svg_map: BASE_URL + 'data/mapit2/' + value.svg,
      svg_map: MEDIA_STORAGE_URI + '/' + value.svg,
      title: value.title,
      objsGroup: objsGroup,
      interactiveObjs: [],
      escalatorsObjs: [],
      escalatorsNodes: {},
      escalatorMeshes: [],
      route_points: [],
      route_tube: undefined,
      route_texture: null,
      routeMeshes: [],
      route_active: false,
    });
  });
  const DEFAULT_CAMERA_FOV = 75;
  const DEFAULT_CAMERA_FAR = 10000;
  config.CAMERA.far = DEFAULT_CAMERA_FAR;
  config.CAMERA.fov = DEFAULT_CAMERA_FOV;
  const fovCorrection = DEFAULT_CAMERA_FOV / config.CAMERA.fov;
  config.CAMERA.maxDistance *= fovCorrection;
  config.CAMERA.far *= fovCorrection;

  const processedConfig: IConfig = {
    ACCENT_COLOR: hex_to_color(response.settings.ACCENT_COLOR ? response.settings.ACCENT_COLOR : config.ACCENT_COLOR),
    BASE_COLOR: hex_to_color(response.settings.BASE_COLOR ? response.settings.BASE_COLOR : config.BASE_COLOR),
    BIG_STORE_DEFAULT_COLOR: hex_to_color(response.settings.BIG_STORE_DEFAULT_COLOR ? response.settings.BIG_STORE_DEFAULT_COLOR : config.BIG_STORE_DEFAULT_COLOR),
    BOUNDARY_COLOR: hex_to_color(response.settings.BOUNDARY_COLOR ? response.settings.BOUNDARY_COLOR : config.BOUNDARY_COLOR),
    BOUNDARY_THICKNESS: parseFloat(response.settings.BOUNDARY_THICKNESS ? response.settings.BOUNDARY_THICKNESS : config.BOUNDARY_THICKNESS),
    BUILDING_BASE_COLOR: hex_to_color(config.BUILDING_BASE_COLOR),
    CAMERA: {
      ...config.CAMERA,
      minZoom: parseInt(response.settings.MIN_ZOOM) ? parseInt(response.settings.MIN_ZOOM) : config.CAMERA.minZoom,
      maxZoom: parseInt(response.settings.MAX_ZOOM) ? parseInt(response.settings.MAX_ZOOM) : config.CAMERA.maxZoom,
      minDistance: parseInt(response.settings.CAMERA_MIN_DISTANCE) ? parseInt(response.settings.CAMERA_MIN_DISTANCE) : config.CAMERA.minDistance,
      maxDistance: parseInt(response.settings.CAMERA_MAX_DISTANCE) ? parseInt(response.settings.CAMERA_MAX_DISTANCE) : config.CAMERA.maxDistance,
    },
    CAMERA_CONTROLS_STATES: (() => {
      const canvasWidth = window.screen.width;
      if (config.CAMERA_CONTROLS_STATES) {
        console.debug('config', { ...config.CAMERA_CONTROLS_STATES })
        return { ...config.CAMERA_CONTROLS_STATES };
      } else {

        const roles: TRoles[] = ["PORTAL", "PORTAL_RESPONSIVE"]

        if (!roles.includes(config.ROLE) && canvasWidth) {
          if (canvasWidth <= 600) {
            return response.camera_controls_states.mobile;
          } else if (canvasWidth <= 1024) {
            return response.camera_controls_states.tablet;
          }
        }
        return response.camera_controls_states[config.DEVICE];
      }
    })(),
    // CAMERA_CONTROLS_STATES: config.CAMERA_CONTROLS_STATES ? config.CAMERA_CONTROLS_STATES : response.camera_controls_states[config.DEVICE],
    CENTER_ID: config.CENTER_ID,
    CONTROLS: null,
    DEBUG: config.DEBUG, // config.DEBUG === 'true' || config.DEBUG === '1'
    DEFAULT_CAMERA_POSITION: config.DEFAULT_CAMERA_POSITION,
    DEFAULT_CONTROLS_TARGET: config.DEFAULT_CONTROLS_TARGET,
    DEFAULT_SELECTED_STORE: config.DEFAULT_SELECTED_STORE,
    DEVICE: config.DEVICE,
    KIOSK: config.KIOSK ? config.KIOSK : '',
    KIOSKS: KIOSKS,
    MAP_BACKGROUND_COLOR: hex_to_color(response.settings.MAP_BACKGROUND_COLOR ? response.settings.MAP_BACKGROUND_COLOR : config.MAP_BACKGROUND_COLOR),
    ORIGINAL_CAMERA_POSITION: config.ORIGINAL_CAMERA_POSITION,
    OVERLAY_COLOR: hex_to_color(response.settings.OVERLAY_COLOR ? response.settings.OVERLAY_COLOR : config.OVERLAY_COLOR),
    OVERLAY_OPACITY: parseFloat(response.settings.OVERLAY_OPACITY ? response.settings.OVERLAY_OPACITY : config.OVERLAY_OPACITY),
    ROLE: config.ROLE,
    SELECTED_RETAILER_SLUG: config.SELECTED_RETAILER_SLUG,
    STATS: config.STATS === 'true' || config.STATS === '1',
    STORE_DEFAULT_COLOR: hex_to_color(response.settings.STORE_DEFAULT_COLOR ? response.settings.STORE_DEFAULT_COLOR : config.STORE_DEFAULT_COLOR),
    STORE_HEIGHT: response.settings.STORE_HEIGHT ? response.settings.STORE_HEIGHT : config.STORE_HEIGHT ? config.STORE_HEIGHT : '8',
    STORE_TEXT_COLOR: hex_to_color(response.settings.STORE_TEXT_COLOR ? response.settings.STORE_TEXT_COLOR : config.STORE_TEXT_COLOR),
    STYLE: response.settings.STYLE ? response.settings.STYLE : config.STYLE ? config.STYLE : '3D',
    WALL_COLOR: hex_to_color(response.settings.WALL_COLOR ? response.settings.WALL_COLOR : config.WALL_COLOR),
    WALL_THICKNESS: parseFloat(response.settings.WALL_THICKNESS ? response.settings.WALL_THICKNESS : config.WALL_THICKNESS),
    FLOORS: response.floors,
    AMENITIES: response.amenities,
    RETAILERS: response.retailers,
    AMENITIES_NAV_BG_COLOR: hex_to_color(response.settings.AMENITIES_NAV_BG_COLOR),
    AMENITIES_NAV_ICON_COLOR: hex_to_color(response.settings.AMENITIES_NAV_ICON_COLOR),
    KIOSK_SIZE: response.settings.KIOSK_SIZE ? response.settings.KIOSK_SIZE : config.KIOSK_SIZE,
    ZOOM_STEP: response.settings.ZOOM_STEP ? response.settings.ZOOM_STEP : config.ZOOM_STEP,
    SELECTED_ZOOM_LIMIT: config.SELECTED_ZOOM_LIMIT ? config.SELECTED_ZOOM_LIMIT : response.settings.SELECTED_ZOOM_LIMIT ? response.settings.SELECTED_ZOOM_LIMIT : '4800',
    SELECTED_ZOOM_LIMIT_DISTANCE: parseInt(response.settings.SELECTED_ZOOM_LIMIT_DISTANCE) ? parseInt(response.settings.SELECTED_ZOOM_LIMIT_DISTANCE) : parseInt(response.settings.CAMERA_MIN_DISTANCE) ? parseInt(response.settings.CAMERA_MIN_DISTANCE) : config.CAMERA.minDistance,
  };

  // AMENITIES_NAV_BG_COLOR: string;
  // AMENITIES_NAV_ICON_COLOR: string;

  return processedConfig;
}

