const useMeshFloors = (data: MapIt2Response | null, jsonConfig?: Partial<IJsonConfig>, role?: TRoles, mediaStorageURI?: string): IMeshParamsTmp => {
  const { floorsData } = useMapBoxContext();
  const { allIndexedMapObjects, allIndexedRetailers, allMapObjects, allNodesFloor, pathFinderGraph, setPathFinderGraph } = useMapBoxContext();
  const [meshParams, setMeshParams] = useState<IMeshValues[][]>([]);
  const [textParams, setTextParams] = useState<Array<{ textMesh: IExtMesh }[]>>([]);
  const [storeLogos, setStoreLogos] = useState<{ storeLogo: Mesh }[][]>([]);
  // const [pathFinderGraph, setPathFinderGraph] = useState<Graph & EventedType>();
  const [escalatorNodes, setEscalatorNodes] = useState<string[]>([]);
  const [urls, setUrls] = useState<string[]>([]);
  const [processedConfig, setProcessedConfig] = useState<IConfig | null>(null);
  let result = useLoader(SVGLoader, urls);

  MEDIA_STORAGE_URI = useMemo(() => {
    const r = /\/$/;
    if (mediaStorageURI) {
      return mediaStorageURI?.replace(r, '');
    }

    return '';

  }, [mediaStorageURI])

  // const myFont = useLoader(FontLoader, 'assets/threejs/threejs/examples/fonts/optimer_regular.typeface.json')
  const myFont = useFont(fontData as unknown as FontData);

  // const getWallShape = (...params: any): Shape | Shape[] => {
  //     return []
  // }


  const handleAsyncStoreLogos = useCallback((floorIndex: number) => ((meshLogo: { storeLogo: Mesh }) => {
    setStoreLogos((prevLogos) => {
      // console.debug({floorIndex});
      if (prevLogos[floorIndex]) {
        prevLogos[floorIndex].push({ ...meshLogo });
      } else {
        prevLogos[floorIndex] = [{ ...meshLogo }];
      }
      // console.debug({prevLogos});
      return [...prevLogos];
    });
  }), [])

  const mergedConfig = useMemo(() => {
    let result = config;

    if (jsonConfig) {
      result = {
        ...config,
        ...jsonConfig,
      }

      if (jsonConfig.CAMERA) {
        result.CAMERA = {
          ...config.CAMERA,
          ...jsonConfig.CAMERA,
        }
      }
    }
    if (!jsonConfig?.DEVICE && result.ROLE === 'WP_SITE') {
      result.DEVICE = 'desktop';
    }
    return result;

  }, [jsonConfig])

  useEffect(() => {
    if (!data) return;


    // console.log('useMeshFloors[data]', {data})

    const processedConfig = init(allIndexedMapObjects, allIndexedRetailers, mergedConfig, floorsData, data as MapIt2Response);
    const values: string[] = [];
    processedConfig.FLOORS.forEach((value) => {
      // values.push(`${MEDIA_STORAGE_URI}/${value.svg}`);
      //@ts-ignore
      values.push(`${value.svg}`);
    });

    setProcessedConfig(processedConfig);
    setUrls([...values]);
    // }, [JSON.stringify(data)]);
  }, [data, allIndexedMapObjects, allIndexedRetailers, floorsData, jsonConfig, mergedConfig]);

  const clearScene = useCallback(() => {
    if (meshParams) {
      meshParams.forEach(floor => {
        if (floor) {
          floor.forEach(value => {
            value.mesh.removeFromParent();
          })
        }
      })
    }

    if (textParams) {
      textParams.forEach(floor => {
        if (floor) {
          floor.forEach(value => {
            value.textMesh.removeFromParent();
          })
        }
      })
    }

    if (storeLogos) {
      storeLogos.forEach(floor => {
        if (floor) {
          floor.forEach(value => {
            value.storeLogo.removeFromParent();
          })
        }
      })
    }

    setMeshParams([]);
    setTextParams([]);
    setStoreLogos([]);
  }, [meshParams, textParams, storeLogos])

  useEffect(() => {
    return () => {
      clearScene();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (!processedConfig || !urls.length || !myFont || !result) return;

    const { GeometriesAndMaterials, graph, escalator_nodes } = loadFloors(allIndexedMapObjects, allIndexedRetailers, allMapObjects, allNodesFloor, pathFinderGraph, floorsData, processedConfig, result, role);
    const TextsAndLogos: Array<{ textMesh: IExtMesh }[]> = [];
    allNonIndexedMapObjects.forEach((params) => {
      let values: IMeshValues | undefined;
      for (let i = 0; i < GeometriesAndMaterials.length; i++) {
        //@ts-ignore
        values = GeometriesAndMaterials[i].find(item => item.object_id === params.map_obj_name);
        if (values !== undefined) {
          break;
        }
      }
      if (!values) return null;
      const textParams = get_store_name_logo_geo(values.geometry, params.map_obj_name, values.floor_index, textLogoNamePrefix, allIndexedMapObjects, allIndexedRetailers, config, myFont, floorsData, handleAsyncStoreLogos(values.floor_index));
      if (textParams) {
        if (!TextsAndLogos[values.floor_index]) {
          TextsAndLogos[values.floor_index] = [];
        }
        TextsAndLogos[values.floor_index].push(textParams);
      }
    })
    const GAMCount = GeometriesAndMaterials.reduce((acc, item) => {
      return (acc + item.length);
    }, 0);
    const countNonIndexedObjs = GAMCount - allNonIndexedMapObjects.length;
    if (countNonIndexedObjs !== 0) {
      const mapObjNames = new Set<string>();
      allNonIndexedMapObjects.forEach((item) => {
        mapObjNames.add(item.map_obj_name);
      });
      GeometriesAndMaterials.forEach((items) => {
        items.forEach(item => {
          if (!mapObjNames.has(item.object_id)) {
            const textParams = get_store_name_logo_geo(item.geometry, item.object_id, item.floor_index, textLogoNamePrefix, allIndexedMapObjects, allIndexedRetailers, config, myFont, floorsData, handleAsyncStoreLogos(item.floor_index));
            if (textParams) {
              if (!TextsAndLogos[item.floor_index]) {
                TextsAndLogos[item.floor_index] = [];
              }
              TextsAndLogos[item.floor_index].push(textParams);
            }
          }
        })
      });
    }
    setMeshParams([...GeometriesAndMaterials]);
    setTextParams(TextsAndLogos);
    setPathFinderGraph(graph);
    setEscalatorNodes([...escalator_nodes]);

  }, [processedConfig, urls, result, myFont, allIndexedMapObjects, allIndexedRetailers, allMapObjects, allNodesFloor, pathFinderGraph, floorsData, role, setPathFinderGraph, handleAsyncStoreLogos]);

  return useMemo(() => {
    return {
      config: processedConfig,
      meshParams: meshParams,
      textParams,
      storeLogos,
      floors: floorsData,
      pathFinderGraph,
      escalator_nodes: escalatorNodes,
    }
  }, [processedConfig, meshParams, textParams, storeLogos, floorsData, pathFinderGraph, escalatorNodes])
}

export default useMeshFloors