import { IAmenitiesInteractiveList, IFloorSelectorProps, TRoles } from "src/types";
import { MouseEventHandler, useEffect, useMemo } from "react";
import styles from '../../MapBox.module.scss';
import { MapObj } from "src/mapitApiTypes";
import { AmenityID } from "src/MapBox";
import "../../assets/icomoon/style.css";

const UIComponent = ({
  floors,
  accentColor,
  selectedFloorIndex,
  handleFloorChange,
  amenitiesList,
  zoomIn,
  zoomOut,
  reset,
  clearRoute,
  handleAmenityClick,
  role,
  mapObjs,
  onExtractedAmenities,
}: IFloorSelectorProps & {
  amenitiesList: IAmenitiesInteractiveList[],
  zoomIn?: MouseEventHandler<HTMLDivElement>,
  zoomOut?: MouseEventHandler<HTMLDivElement>,
  reset?: (fullReset?: boolean) => void,
  clearRoute?: MouseEventHandler<HTMLDivElement>,
  handleAmenityClick: (type: string) => void,
  role?: TRoles,
  mapObjs?: MapObj[],
  onExtractedAmenities?: (amenityIds: AmenityID[]) => void
}
) => {
  const handleResetClick: MouseEventHandler = (e) => {
    e.preventDefault();
    if (reset instanceof Function) {
      if (role === 'PORTAL_RESPONSIVE' || role === 'PORTAL') {
        reset(true)
      }
      else {
        reset();
      }
    }
  }

  const extractValues = useMemo<AmenityID[]>(() => {

    if (!mapObjs) {
      return [];
    }

    return mapObjs
      .filter(obj => obj.layer_type === "amenity")
      .map(obj => obj.value as AmenityID);
  }, [mapObjs])
  useEffect(() => {
    if (onExtractedAmenities) {
      onExtractedAmenities(extractValues)
    }
  }, [mapObjs, onExtractedAmenities, extractValues])

  return (
    <div id={styles['threejs-map-wrapper']}>
      <div id={styles['threejs-map']}>
        {floors.length > 0 && (
          <div className={`map_controls ${styles.map_controls}`}>
            <div className="map_controls_wrapper">
              {floors.length > 1 && (
                <div className="floors btn_group">
                  {floors.map((floor: any, index: number) => (
                    <div key={index} className={`floor control_btn ${index === selectedFloorIndex ? 'active' : ''}`} onClick={() => {
                      handleFloorChange(index)
                    }}>{floor.title}</div>
                  ))}
                </div>
              )}

              <div className="reset-zoom">
                <div onClick={handleResetClick} className="reset_btn control_btn">RESET</div>

                <div className="zoom_btns">
                  <div onClick={zoomOut} className="zoom-out control_btn">–</div>
                  <div onClick={zoomIn} className="zoom-in control_btn">+</div>
                </div>
              </div>
            </div>
          </div>
        )}

        <div className={`loading_map ${styles['hide']}`}>Loading map, please wait...</div>

        {!['PORTAL', 'DISPLAY_APP'].includes(role as string) &&
          <div className={`amenities ${styles.amenities}`} style={{ display: 'block' }}>
            {amenitiesList.map((amenity, index) => {
              if (!extractValues.includes(amenity.type)) {
                return null;
              }
              return (
                <div key={`amenity-${amenity.name}-${index}`} className={`${styles.amenity}`} style={{ backgroundColor: '#000000' }} onClick={() => handleAmenityClick(amenity.type)}>
                  <span className={`${styles.icon} icon icon-amenity-${amenity.icon}`}></span>
                </div>
              )
            })}
          </div>
        }
      </div>
    </div>
  )
}

export default UIComponent;
