import { Dispatch, ReactNode, SetStateAction, createContext, useContext, useState } from "react"
import createGraph, { Graph } from "ngraph.graph";
import { IRetailer, MapObj } from "src/mapitApiTypes";
import type { Mesh } from "three";
import path from "ngraph.path";
import { EventedType } from "ngraph.events";
import { IFloorData } from "src/types";


type MapBoxContextType = {
  meshByObjectId: Map<string, Mesh>;
  setMeshByObjectId: Dispatch<SetStateAction<Map<string, Mesh>>> | Function;
  allMapObjects: Array<string>;
  setAllMapObjects: Dispatch<SetStateAction<Array<string>>> | Function;
  allIndexedMapObjects: Record<string, MapObj>;
  setAllIndexedMapObjects: Dispatch<SetStateAction<Record<string, MapObj>>> | Function;
  allIndexedRetailers: Record<string, IRetailer>;
  setAllIndexedRetailers: Dispatch<SetStateAction<Record<string, IRetailer>>> | Function;
  allNodesFloor: Record<string, number>;
  setAllNodesFloor: Dispatch<SetStateAction<Record<string, number>>> | Function;
  pathFinderGraph: Graph<any, any> & EventedType
  setPathFinderGraph: Dispatch<SetStateAction<Graph<any, any> & EventedType>> | Function;
  ngraphPath: typeof path;
  setNgraphPath: Dispatch<SetStateAction<typeof path>> | Function;
  floorsData: IFloorData[];
}
const MapBoxContext = createContext<MapBoxContextType>({
  meshByObjectId: new Map<string, Mesh>(),
  setMeshByObjectId: () => null,
  allMapObjects: [],
  setAllMapObjects: () => null,
  allIndexedMapObjects: {},
  setAllIndexedMapObjects: () => null,
  allIndexedRetailers: {},
  setAllIndexedRetailers: () => null,
  allNodesFloor: {},
  setAllNodesFloor: () => null,
  pathFinderGraph: createGraph(),
  setPathFinderGraph: () => null,
  ngraphPath: path,
  setNgraphPath: () => null,
  floorsData: []
})

const MapBoxContextProvider = ({ children }: { children: ReactNode }) => {

  const [meshByObjectId, setMeshByObjectId] = useState<MapBoxContextType['meshByObjectId']>(new Map<string, Mesh>());
  const [allMapObjects, setAllMapObjects] = useState<MapBoxContextType['allMapObjects']>([]);
  const [allIndexedMapObjects, setAllIndexedMapObjects] = useState<MapBoxContextType['allIndexedMapObjects']>({});
  const [allIndexedRetailers, setAllIndexedRetailers] = useState<MapBoxContextType['allIndexedRetailers']>({});
  const [allNodesFloor, setAllNodesFloor] = useState<MapBoxContextType['allNodesFloor']>({});

  //<{ x:number, y:number }, { weight: number }>
  const [pathFinderGraph, setPathFinderGraph] = useState<MapBoxContextType['pathFinderGraph']>(createGraph());
  const [ngraphPath, setNgraphPath] = useState<MapBoxContextType['ngraphPath']>(path);
  const [floorsData] = useState<MapBoxContextType['floorsData']>([]);

  return (
    <MapBoxContext.Provider value={{
      meshByObjectId,
      setMeshByObjectId,
      allMapObjects,
      setAllMapObjects,
      allIndexedMapObjects,
      setAllIndexedMapObjects,
      allIndexedRetailers,
      setAllIndexedRetailers,
      allNodesFloor,
      setAllNodesFloor,
      pathFinderGraph,
      setPathFinderGraph,
      ngraphPath,
      setNgraphPath,
      floorsData
    }}
    >
      {children}
    </MapBoxContext.Provider>
  )

}

export function useMapBoxContext() {
  const context = useContext(MapBoxContext);

  if (context === undefined) {
    throw new Error("useMapBoxContext must be used within MapBoxContextProvider");
  }

  return context;
}

export default MapBoxContextProvider;
