import { retailersApi, useGetRetailersQuery, useLazyGetRetailersQuery } from "src/services/retailersApi"
import { fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { useCallback, useEffect, useState } from "react";
import { IRetailer } from "src/mapitApiTypes";

const useAllRetailers = (baseUrl: string, center_id: string, limit: number) => {
  const [retailers, setRetailers] = useState<{ count: number, items: IRetailer[] }>({ count: 0, items: [] })
  const baseQuery = baseUrl?.lastIndexOf('/') === baseUrl.length - 1 ? baseUrl.slice(0, -1) : baseUrl;

  const [getRetailers] = useLazyGetRetailersQuery()

  const fetchAllRetailers = useCallback(async (page: number, totalPages: number) => {
    const requests = [];
    const result: { count: number, items: IRetailer[] } = { count: 0, items: [] };
    if (totalPages >= page) {
      for (let currentPage = page; currentPage <= totalPages; currentPage++) {
        requests.push(getRetailers({ baseQuery, center_id, limit, page: currentPage }, true));
      }
      if (requests.length) {
        const data = await Promise.all(requests);
        data.forEach(item => {
          if (item && item.data && item.data.items.length) {
            const { count, items } = item.data;
            result.count = count;
            result.items = [
              ...result.items as IRetailer[],
              ...items as IRetailer[],
            ]
          }
        })
        if (result) {
          setRetailers(ret => ({
            count: result.count,
            items: [...ret.items, ...result.items],
          }))
        }
        return { ...result };
      }
    } else {
      return { ...result };
    }
  }, [baseQuery, center_id, getRetailers, limit])

  const getAll = useCallback(() => new Promise<{ count: number, items: IRetailer[] }>((resolve, reject) => {
    let result: { count: number, items: IRetailer[] } = { count: 0, items: [] };
    getRetailers({ baseQuery, center_id, limit, page: 1 }, true).then(data => {
      if (data.status === 'rejected') {
        reject(data.error);
      }
      if (data.data) {
        result = { ...data.data };
        const totalPages = Math.round(result.count / limit);
        if (totalPages > 1) {
          console.debug({ totalPages })
          fetchAllRetailers(2, totalPages).then(data => {
            if (data) {
              resolve({
                count: result.count,
                items: [...result.items, ...(data.items ?? [])],
              })
            } else {
              resolve({ ...result });
            }
          });
        } else {
          resolve({ ...result })
        }
      } else {
        resolve({ ...result });
      }
    });
  }), [baseQuery, center_id, fetchAllRetailers, getRetailers, limit])

  return { retailers, getAll }
}

export default useAllRetailers;