import { useCallback, useEffect, useState } from "react"
import useGetRetailers, { Data } from "../useGetRetailersBySearch";
export type RetailerOption = {
  value: number,
  label: string,
  isDisabled?: boolean,
  isFixed?: boolean,
}
type UseRetailersOptions = (centerId: string, apiURI: string) => {
  isLoading: boolean,
  loadOptions: (inputValue: string, callback: (options: RetailerOption[]) => void) => void,
  retailers: RetailerOption[],
};
const useRetailersOptions: UseRetailersOptions = (centerId, apiURI) => {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [retailers, setRetailers] = useState<any[]>([]);
  const [limit] = useState<number>(100);
  const fetchRetailers = useGetRetailers({ centerId, apiURI });

  const processData = useCallback((data: Data, isLoadingMore?: boolean) => {
    setIsLoading(false);
    let result: any = [];
    if (!data) return result;
    if (!data.isError) {
      result = data.items.map((item: any) => {
        return {
          value: item.id,
          label: `${item.name}${item.location && item.location.length > 1 ? ', ' + item.location : ''}`,
        }
      })
    } else {
      console.error(`Fetch Retailers error: ${data.error}`)
    }
    return result;
  }, []);

  const loadOptions = useCallback((inputValue: string, callback: (options: RetailerOption[]) => void) => {
    callback(retailers.filter(item => (item.label as string).toLowerCase().includes(inputValue)));
  }, [retailers])

  useEffect(() => {
    setIsLoading(true);
    const retailersData = fetchRetailers('', limit);
    retailersData.then(data => {
      const result = processData(data);
      setRetailers([...result]);
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fetchRetailers, limit, processData])

  return {
    isLoading,
    loadOptions,
    retailers,
  }
}

export default useRetailersOptions;