import { useMemo, useState } from "react";
import { IRetailer } from "src/mapitApiTypes";

type RetailerDataProps = {
  centerId: string,
  slug: string,
  webApiURI: string,
}

export type RetailerData = {
  data: IRetailer | null,
  isLoading: boolean,
  isError: boolean,
  error: string | null,
}

const defaultData: RetailerData = {
  data: null,
  isLoading: false,
  isError: false,
  error: null,
}
const useRetailerData = (props: RetailerDataProps): RetailerData => {
  const { centerId, slug, webApiURI } = props;
  const [data, setData] = useState<RetailerData>(defaultData);

  useMemo(() => {
    if (!slug || !centerId) {
      setData({ ...defaultData });
      return false;
    }

    let apiURI = webApiURI;

    if (apiURI) {
      const r = /\/$/;
      apiURI = apiURI.replace(r, '');
      setData({
        isLoading: true,
        data: null,
        isError: false,
        error: null,
      })
      fetch(`${apiURI}/v1/retailers/${slug}`, {
        headers: {
          center_id: centerId,
        }
      }).then(
        (response) => response.json()
      ).then(data => {
        setData({
          isLoading: false,
          isError: false,
          error: null,
          data: {
            id: data.id,
            retail_name: data.name,
            slug: data.slug,
            location: data.location && data.location !== '-' ? data.location : '',
            retailer_phone: data.retailer_phone,
            retailer_description: data.global_retailer.description,
            logo: data.media.url,
            map_obj_name: '',
          }
        });
      }).catch((e: any) => {
        setData({
          data: null,
          isLoading: false,
          isError: true,
          error: `Error fetch retaler data: ${e.message}`,
        })
      })
    } else {
      setData({
        isLoading: false,
        isError: true,
        error: 'webApiURI not provided!',
        data: null,
      })
    }

  }, [centerId, slug, webApiURI]);

  return data;

}

export default useRetailerData;
