import { IConfig, IExtMesh, IFloorData, IMeshParams } from "../../types";
import { Map } from "./Map";
import { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from "react";
import { Group, Mesh, PerspectiveCamera, Vector3 } from "three";
import { ThreeEvent, useFrame, useThree } from "@react-three/fiber";
import { get_camera_focus_object } from "../../helpers/camera.helpers";
import { MapControls } from "@react-three/drei";
import { create_route, make_amenity_route } from "../../helpers/route.helpers";
// import { allIndexedMapObjects } from "../../globals";
import { IZoomData } from "./SceneComponent";
import { defaultVars } from "src/defaults";
import { useMeshObjectContext } from "src/contexts/MeshObjectContextProvider";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { IRetailer, MapObj } from "src/mapitApiTypes";
// import { allNodesFloor } from "src/globals";

interface IFloorsMapProps {
  meshFloors: IMeshParams;
  currentFloorIndex: number;
  onPointerEnter?: (e: ThreeEvent<PointerEvent>) => void;
  onPointerLeave?: (e: ThreeEvent<PointerEvent>) => void;
  onPointerMove?: (e: ThreeEvent<PointerEvent>) => void;
  onClick?: (e: ThreeEvent<MouseEvent>) => void;
  onCameraMove?: (e: any) => void;
  currKioskObj: Mesh;
  currKioskFloorIndex: number;
  routeTargetId?: string;
  routeTargetFromId?: string;
  amenityTargetType: string;
  activeObjectId?: string;
  hoverObjectId?: string;
  handleChangeFloor: (index: number) => void;
  escalatorNodes: string[];
  zoom: IZoomData | null;
  handleCameraLength?: (length: number) => void;
  config: IConfig;
  cameraPos?: { x: number, y: number, z: number } | undefined | null;
  controlPos?: { x: number, y: number, z: number } | undefined | null;
  handleCameraAndControlsChange?: (camPos: { x: number, y: number, z: number }, ctrlTarget: { x: number, y: number, z: number }) => void;
  isSelectedRetailerSlug: boolean;
  setIsCameraInit: Function;
  retailers: IRetailer[],
  mapObjs: MapObj[],
  handleObjectClick: (object: IExtMesh, targetId: string, softClick?: boolean) => void,
  triggerClick: boolean,
}


export const FloorsMap = forwardRef((params: IFloorsMapProps, ref) => {
  const { allIndexedMapObjects, allNodesFloor } = useMapBoxContext();
  const [firstMove, setFirstMove] = useState<boolean>(false);
  const { meshFloors,
    currentFloorIndex,
    currKioskObj,
    currKioskFloorIndex,
    routeTargetId,
    routeTargetFromId,
    amenityTargetType,
    handleChangeFloor,
    escalatorNodes,
    zoom,
    handleCameraLength,
    config,
    handleCameraAndControlsChange,
    activeObjectId,
    isSelectedRetailerSlug,
    setIsCameraInit,
    retailers,
    mapObjs,
    handleObjectClick,
    triggerClick, } = params;
  const { floors } = meshFloors;
  const [cameraLimits, SetCameraLimits] = useState({ min: config.CAMERA.minDistance, max: config.CAMERA.maxDistance, initialDistance: 0, needsUpdate: true })
  const { setCameraLength } = useMeshObjectContext();

  const groupRef = useRef<Group>(null);
  const [routeTubes, setRouteTubes] = useState<Mesh[]>([]);
  const mapControlRef = useRef<any>(null);
  const { camera, scene } = useThree();
  const cameraFocus = useRef<{
    position: Vector3, target: Vector3,
    fromPosition: Vector3, fromTarget: Vector3,
    animationStartTime: number, duration: number
  } | null>(null);
  // const cameraFocus = useRef<{ position:Vector3, target:Vector3, animationStartTime:number, cameraTime:number, targetTime:number } | null>(null);

  const style = meshFloors.config.STYLE;
  const accentColor = meshFloors.config.ACCENT_COLOR;
  const pathFinderGraph = meshFloors.pathFinderGraph;
  const from = currKioskObj ? (currKioskObj as IExtMesh).object_id :
    routeTargetFromId ? routeTargetFromId : undefined;
  const isUseDefaultsLimits = config.ROLE === "PORTAL" && routeTargetId;

  const resetCamera = useCallback((fullReset: boolean = false) => {
    if (!(camera instanceof PerspectiveCamera)) {
      console.error('Camera is not a PerspectiveCamera');
      return;
    }
    const selectedFloorMeshParams = meshFloors.meshParams[currKioskFloorIndex];
    if (!selectedFloorMeshParams?.length) {
      console.warn('objects not loaded yet');
      return;
    }

    let meshes: Mesh[];
    meshes = selectedFloorMeshParams.map(item => item.mesh);


    const focus = get_camera_focus_object(meshes, camera.fov, camera.aspect, config.ROLE === 'PORTAL' ? '2D' : style);


    let isZeroCCS = true;

    if (config.CAMERA_CONTROLS_STATES) {
      let cameraV3 = config.CAMERA_CONTROLS_STATES.camera;
      if (!(cameraV3 instanceof Vector3)) {
        cameraV3 = new Vector3(cameraV3.x, cameraV3.y, cameraV3.z);
      }
      isZeroCCS = (cameraV3 as Vector3).length() === 0;
    }

    if (!fullReset && config.CAMERA_CONTROLS_STATES && !isZeroCCS && !['PORTAL'].includes(config.ROLE)) {
      focus.position.set(config.CAMERA_CONTROLS_STATES.camera.x, config.CAMERA_CONTROLS_STATES.camera.y, config.CAMERA_CONTROLS_STATES.camera.z);
      focus.target.set(config.CAMERA_CONTROLS_STATES.controls.x, config.CAMERA_CONTROLS_STATES.controls.y, config.CAMERA_CONTROLS_STATES.controls.z);
    }

    const cameraDistance = Math.min(
      cameraLimits.max,
      Math.max(
        cameraLimits.min,
        focus.position.distanceTo(focus.target)
      )
    );
    focus.position.sub(focus.target).setLength(cameraDistance).add(focus.target);

    cameraFocus.current = {
      ...focus,
      fromPosition: mapControlRef.current.object.position.clone(),
      fromTarget: mapControlRef.current.target.clone(),
      animationStartTime: performance.now(),
      duration: config.CAMERA.animSpeed, // how long it takes to move camera to get to new position
    };
    if (['DISPLAY_APP', 'WP_SITE'].includes(config.ROLE) && cameraLimits.needsUpdate === false && cameraLimits.initialDistance > 0) {
      SetCameraLimits(cl => ({
        ...cl,
        min: config.CAMERA.minDistance < 0.1 ? 0.1 : config.CAMERA.minDistance,
        max: config.CAMERA.maxDistance,
      }));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [camera, cameraLimits.needsUpdate, config.CAMERA.animSpeed, config.CAMERA.maxDistance, config.CAMERA.minDistance, config.CAMERA_CONTROLS_STATES, config.ROLE, currKioskFloorIndex, meshFloors.meshParams, style]);

  const focusDistance = cameraFocus.current ? cameraFocus.current.position.distanceTo(cameraFocus.current.target) : 0;

  useEffect(() => {

    if (focusDistance > 0 && cameraLimits.needsUpdate) {
      SetCameraLimits({ min: config.CAMERA.minDistance < 0.1 ? 0.1 : config.CAMERA.minDistance, max: config.CAMERA.maxDistance, initialDistance: focusDistance, needsUpdate: false })
      setIsCameraInit(true);
    }

  }, [focusDistance, cameraLimits.needsUpdate, config.CAMERA])

  useEffect(() => {

    if (['DISPLAY_APP', 'WP_SITE'].includes(config.ROLE) && routeTargetId && cameraLimits.initialDistance > 0) {
      SetCameraLimits(cl => {
        const minDistance = Number(config.SELECTED_ZOOM_LIMIT_DISTANCE) < config.CAMERA.minDistance ? config.CAMERA.minDistance : Number(config.SELECTED_ZOOM_LIMIT_DISTANCE)
        return {
          ...cl,
          needsUpdate: false,
          min: minDistance < 0.1 ? 0.1 : minDistance,
          max: config.CAMERA.maxDistance,
        }
      });
    }
  }, [config.CAMERA.maxZoom, config.CAMERA.minZoom, config.SELECTED_ZOOM_LIMIT, routeTargetId, cameraLimits.initialDistance])


  useEffect(() => {
    if (meshFloors.meshParams.length > 0) {
      resetCamera();
    }
  }, [meshFloors.meshParams.length, resetCamera])

  useEffect(() => {
    if (!amenityTargetType || config.ROLE === 'PORTAL') {
      setRouteTubes([]);
      return;
    }
    const route = make_amenity_route(allNodesFloor, from ?? '', amenityTargetType, scene, allIndexedMapObjects, pathFinderGraph, floors, escalatorNodes, style);
    setRouteTubes(route);

    if (currKioskObj && config.ROLE === "DISPLAY_APP") {
      handleChangeFloor(currKioskFloorIndex);
    }

  }, [amenityTargetType, scene, pathFinderGraph, floors, escalatorNodes, style, from, config.ROLE, allNodesFloor, allIndexedMapObjects])

  const clearEscalators = useCallback(() => {
    meshFloors.floors.flat().forEach((item) => item.escalatorMeshes = []);
  }, [meshFloors])

  useEffect(() => {

    if (!from || !routeTargetId || config.ROLE === 'PORTAL') {
      if (config.ROLE === 'PORTAL') {
        if (routeTargetId) {
          const targetMesh = scene.getObjectByProperty('object_id', routeTargetId);
          if (targetMesh) {
            setRouteTubes([targetMesh as IExtMesh])
          }
        } else {
          clearEscalators();
          setRouteTubes([]);
        }
      } else if (!amenityTargetType) {
        clearEscalators();
        setRouteTubes([]);
      }
      return;
    }
    if (!amenityTargetType) {
      floors.forEach(floor => {
        floor.escalatorMeshes = [];
      });
    }

    // console.log('Make route from %s to %s', from, routeTargetId, pathFinderGraph);

    /* MAKE ROUTE PATH */

    const route = create_route(allNodesFloor, from, routeTargetId, scene, floors, meshFloors.escalator_nodes, pathFinderGraph, style);

    setRouteTubes(route);

  }, [from, routeTargetId, scene, camera, floors, accentColor, pathFinderGraph, style, meshFloors.escalator_nodes, amenityTargetType, config.ROLE, allNodesFloor, triggerClick]);

  const selectedFloorMeshParams = meshFloors.meshParams[currentFloorIndex];
  useEffect(() => {
    if (config.ROLE === 'PORTAL_RESPONSIVE') return;

    if (!(camera instanceof PerspectiveCamera)) {
      console.error('Camera is not a PerspectiveCamera');
      return;
    }
    if (!selectedFloorMeshParams?.length) {
      console.warn('objects not loaded yet');
      return;
    }

    let routeMeshes = floors[currentFloorIndex]?.routeMeshes

    if (!activeObjectId && !amenityTargetType) {
      return;
    }

    let meshes: Mesh[] = [];
    if (routeTubes[currentFloorIndex] && routeMeshes?.length) {

      meshes = [...routeMeshes]
    } else if (activeObjectId && (config.ROLE === 'PORTAL' || isSelectedRetailerSlug)) {
      const currentActiveObject = routeTargetId && selectedFloorMeshParams.find(item => item.mesh.object_id === routeTargetId);
      if (currentActiveObject) {
        meshes = [currentActiveObject.mesh];
      }
    }
    if (!meshes.length) {
      meshes = selectedFloorMeshParams.map(item => item.mesh);
    }

    const focus = get_camera_focus_object(meshes, camera.fov, camera.aspect, config.ROLE === 'PORTAL' || config.ROLE === 'WP_SITE' ? '2D' : style);
    const cameraDistance = Math.min(
      isUseDefaultsLimits ? defaultVars.CAMERA.maxDistance : cameraLimits.max,
      Math.max(
        isUseDefaultsLimits ? defaultVars.CAMERA.minDistance : cameraLimits.min,
        focus.position.distanceTo(focus.target)
      )
    );
    focus.position.sub(focus.target).setLength(cameraDistance).add(focus.target);
    cameraFocus.current = {
      ...focus,
      fromPosition: mapControlRef.current.object.position.clone(),
      fromTarget: mapControlRef.current.target.clone(),
      animationStartTime: performance.now(),
      duration: config.CAMERA.animSpeed, // how long it takes to move camera to get to new position
    };

    // return () => {
    //     cameraFocus.current = null;
    // };
  }, [routeTubes, selectedFloorMeshParams, currentFloorIndex, style, camera, routeTargetId, config.ROLE, config.CAMERA.maxDistance, config.CAMERA.minDistance, config.CAMERA.animSpeed, floors, activeObjectId, amenityTargetType, isSelectedRetailerSlug]);

  useEffect(() => {
    if (zoom?.direction && mapControlRef.current) {
      if (!(camera instanceof PerspectiveCamera)) {
        console.error('Camera is not a PerspectiveCamera');
        return;
      }

      if (!selectedFloorMeshParams.length) {
        console.warn('objects not loaded yet');
        return;
      }

      let meshes: Mesh[];
      if (routeTubes[currentFloorIndex]) {
        meshes = [routeTubes[currentFloorIndex]];

        const currentActiveObject = routeTargetId && selectedFloorMeshParams.find(item => item.mesh.object_id === routeTargetId);
        if (currentActiveObject) {
          meshes.push(currentActiveObject.mesh);
        }
      } else {
        meshes = selectedFloorMeshParams.map(item => item.mesh);
      }
      const focus = {
        position: mapControlRef.current.object.position.clone(),
        target: mapControlRef.current.target.clone()
      };

      let cameraDistance = focus.position.distanceTo(focus.target);
      const zoomStep = config.ZOOM_STEP ? parseInt(config.ZOOM_STEP) : parseInt(defaultVars.ZOOM_STEP as string);

      if (zoom.direction === 'in') {
        cameraDistance -= zoomStep;
      } else {
        cameraDistance += zoomStep;
      }

      cameraDistance = Math.min(
        cameraLimits.max,
        Math.max(
          cameraLimits.min,
          cameraDistance
        )
      );

      focus.position.sub(focus.target).setLength(cameraDistance).add(focus.target);

      cameraFocus.current = {
        ...focus,
        fromPosition: mapControlRef.current.object.position.clone(),
        fromTarget: mapControlRef.current.target.clone(),
        animationStartTime: performance.now(),
        duration: config.CAMERA.animSpeed, // how long it takes to move camera to get to new position
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [zoom])

  // console.log('CameraLimits', cameraLimits)

  const getInitialDistance = () => {
    return cameraLimits.initialDistance
  }


  const onCameraMove = useCallback((e: any) => {
    if (handleCameraLength) {
      const vector = new Vector3();
      vector.copy(e.target.object.position);
      vector.sub(mapControlRef.current.target);
      handleCameraLength(vector.length());
      setCameraLength(vector.length());
      if (handleCameraAndControlsChange) {
        handleCameraAndControlsChange(mapControlRef.current.object.position.clone(), mapControlRef.current.target.clone())
      }
    }
  }, [handleCameraLength, handleCameraAndControlsChange, setCameraLength])

  useFrame(() => {
    if (cameraFocus.current?.duration && cameraFocus.current?.duration > 0) {
      if (!firstMove) {
        setFirstMove(true);
        cameraFocus.current.duration = 0;
      }
      const focus = cameraFocus.current;
      const animationTime = (performance.now() - focus.animationStartTime) / 1000;

      const animationProgress = Math.min(1, animationTime / focus?.duration);
      mapControlRef.current.object.position.lerpVectors(focus.fromPosition, focus.position, animationProgress);
      mapControlRef.current.target.lerpVectors(focus?.fromTarget, focus.target, animationProgress);

      mapControlRef.current.object.updateProjectionMatrix();
      if (animationProgress >= 1) {
        cameraFocus.current = null;
      }
    }
  })

  const createRouteToStore = useCallback((retailerSlug: string) => {
    if (retailerSlug.length) {
      const retailer = retailers.find(item => item.slug === retailerSlug);
      if (retailer) {
        const mapObj = mapObjs.find(item => item.retailer_id === retailer.id);
        if (mapObj) {
          const obj = scene.getObjectByProperty('object_id', mapObj.map_obj_name);
          if (obj) {
            handleObjectClick(obj as IExtMesh, mapObj.map_obj_name, true);
          }
        }
      }
    }
  }, [handleObjectClick, mapObjs, retailers, scene]);

  useImperativeHandle(ref, () => ({
    resetCamera,
    createRouteToStore,
    getInitialDistance,
  }))


  return (<group rotation={[Math.PI / 2, 0, 0]} ref={groupRef}>
    <>
      <MapControls
        makeDefault
        onChange={onCameraMove}
        ref={mapControlRef}
        maxPolarAngle={Math.PI / 2}
        minDistance={isUseDefaultsLimits ? defaultVars.CAMERA.minDistance : cameraLimits.min}
        maxDistance={isUseDefaultsLimits ? defaultVars.CAMERA.maxDistance : cameraLimits.max}
        enableZoom={config.ROLE === "PORTAL" || config.ROLE === "DISPLAY_APP"}
        enableRotate={config.ROLE !== "WP_SITE" && config.ROLE !== "PORTAL_RESPONSIVE" && config.ROLE !== "DISPLAY_APP"}
        dampingFactor={0.3}
      />

      {floors && floors.map((value: IFloorData, index: number) => (
        <Map
          key={`Map-${index}`}
          visible={currentFloorIndex === index}
          floorIndex={index}
          currentFloorIndex={currentFloorIndex}
          currKioskObj={params.currKioskObj}
          activeObjectId={params.activeObjectId}
          hoverObjectId={params.hoverObjectId}
          meshFloors={meshFloors}
          routeTube={config.ROLE !== 'PORTAL' ? routeTubes && routeTubes[index] : undefined}
          onPointerEnter={params.onPointerEnter}
          onPointerLeave={params.onPointerLeave}
          onPointerMove={params.onPointerMove}
          onClick={params.onClick}
          handleChangeFloor={handleChangeFloor}
        />
      ))}
    </>
  </group>)
})