import { useCallback, useEffect, useState } from "react";
import { IRetailer, MapIt2Response, Settings } from "./mapitApiTypes";
import demoData from './demo/data.json';
import useAllRetailers from "./Hooks/useAllRetailers";
import { amenitiesList } from "./helpers/amenities.helper";

interface useMapIt2DataProps {
  CENTER_ID: string;
  mapitData?: MapIt2Response;
  webApiURI?: string;
}

type Mapit2DataReturn = {
  data: MapIt2Response | null,
  refreshData: () => void,
  removeMissingMapObjects: (objectNames: string[]) => void,
}

/**
 * This is a hook that fetches the mapit2 data from the server by CENTER_ID. or uses the data passed in.
 * @param CENTER_ID
 * @param mapitData
 */
export function useMapit2Data({ CENTER_ID, mapitData, webApiURI }: useMapIt2DataProps): Mapit2DataReturn {
  const [data, setData] = useState<MapIt2Response | null>(null);

  if (!CENTER_ID && !mapitData) {
    console.error('useMapit2Data requires either CENTER_ID or mapitData');
  }

  const { getAll } = useAllRetailers(webApiURI as string, CENTER_ID as string, 100);

  const getData = useCallback(() => {
    let apiURI = webApiURI || null;

    if (apiURI) {
      const r = /\/$/;
      apiURI = apiURI.replace(r, '');
      const mapObjsApiUri = `${apiURI}/v1/mapit2/data`;
      const floorsApiUri = `${apiURI}/v1/mapit2/floors?limit=100&offset=0`;
      const kioskApiUri = `${apiURI}/v1/display_kiosks?limit=100&page=1&display_type_id=1`;
      const settingsApiUri = `${apiURI}/v1/mapit2/settings/${CENTER_ID}`;
      const responsiveApiUri = `${apiURI}/v1/mapit2/responsive/${CENTER_ID}`

      const mapObjsPromise = fetch(mapObjsApiUri, {
        headers: {
          center_id: CENTER_ID,
        },
      }).then(response => {
        if (!response.ok) {
          console.error(`Server error: [${response.status}] [${response.statusText}] [${response.url}]`);
        }
        return response.json()
      }).catch(e => { console.error(e) })

      const floorsPromise = fetch(floorsApiUri, {
        headers: {
          center_id: CENTER_ID,
        },
      }).then(response => {
        if (!response.ok) {
          console.error(`Server error: [${response.status}] [${response.statusText}] [${response.url}]`);
        }
        return response.json()
      })
        .then(data => {
          if (!data
            || !data.items
            || (data.items && !data.items.length)) {
            console.error('Empty floors data from server');
          }
          return data;
        }).catch(e => { console.error(e) })

      const kiosksPromise = fetch(kioskApiUri, {
        headers: {
          center_id: CENTER_ID,
        },
      }).then(response => {
        if (!response.ok) {
          console.error(`Server error: [${response.status}] [${response.statusText}] [${response.url}]`);
        }
        return response.json()
      }).catch(e => { console.error(e) })

      const settingsPromise = fetch(settingsApiUri, {
        headers: {
          center_id: CENTER_ID,
        }
      }).then(response => {
        if (!response.ok) {
          console.error(`Server error: [${response.status}] [${response.statusText}] [${response.url}]`);
        }
        return response.json();
      }).catch(e => { console.error(e) })

      const responsivePromise = fetch(responsiveApiUri, {
        headers: {
          center_id: CENTER_ID,
        }
      }).then(response => {
        if (!response.ok) {
          throw new Error(`Server error: [${response.status}] [${response.statusText}] [${response.url}]`);
        }
        return response.json();
      }).catch(e => { throw new Error(e) })

      Promise.all<Array<any>>([
        // retailersPromise,
        // getAllRetailers(CENTER_ID, 1, 100, apiURI, 0),
        getAll(),
        mapObjsPromise,
        floorsPromise,
        kiosksPromise,
        settingsPromise,
        responsivePromise
      ]).then(data => {
        if (data.length >= 5) {
          const responseData: Partial<MapIt2Response> = {};
          responseData.retailers = data[0].items.map((item: any): IRetailer => ({
            id: item.id,
            retail_name: item.name,
            slug: item.slug,
            location: item.location,
            retailer_phone: '',
            retailer_description: '',
            logo: item.media.url,
            map_obj_name: '',
          }));
          responseData.map_objs = [...data[1].items].map(item => ({
            ...item,
            offsetX: item.offset_x,
            offsetY: item.offset_y,
          }));
          responseData.floors = [...data[2].items];
          responseData.settings = {
            ...demoData.settings as Settings,
            KIOSK_SIZE: "1",
          }
          responseData.kiosks = [];
          responseData.amenities = amenitiesList;
          responseData.kiosks = [...data[3].items];
          const settings: Settings = data[4]?.settings ? data[4].settings : {
            "STYLE": "3D",
            "MAP_BACKGROUND_COLOR": "FFFFFF",
            "ACCENT_COLOR": "4EA5FF",
            "STORE_DEFAULT_COLOR": "E2E2E2",
            "BIG_STORE_DEFAULT_COLOR": "3D3D3D",
            "WALL_THICKNESS": "0.6",
            "WALL_COLOR": "888888",
            "BOUNDARY_THICKNESS": "0.8",
            "BOUNDARY_COLOR": "888888",
            "BASE_COLOR": "25292B",
            "STORE_TEXT_COLOR": "222222",
            "OVERLAY_COLOR": "FFFFFF",
            "OVERLAY_OPACITY": "0.7",
            "AMENITIES_NAV_BG_COLOR": "000000",
            "AMENITIES_NAV_ICON_COLOR": "FFFFFF",
            "ZOOM_STEP": "100",
          };
          if (data[5]) {
            responseData.camera_controls_states = data[5];
          } else {
            responseData.camera_controls_states = { ...demoData.camera_controls_states }
          }
          responseData.settings = settings;

          setData({ ...responseData as MapIt2Response })
        }
      })
    }
  }, [CENTER_ID, getAll, webApiURI])

  useEffect(() => {
    if (mapitData) {
      setData(mapitData);
      return;
    }

    if (!CENTER_ID) {
      return;
    }

    getData();

  }, [CENTER_ID, mapitData, getData]);

  const removeMissingMapObjects = useCallback((objectNames: string[]) => {
    if (data?.map_objs) {
      const mapObjects = data.map_objs.filter(item => {
        return objectNames.includes(item.map_obj_name)
      })
      if (mapObjects.length < data.map_objs.length) {
        setData({
          ...data,
          map_objs: [...mapObjects],
        })
      }
    }
  }, [data]);

  const refreshData = () => getData();
  return { data, refreshData, removeMissingMapObjects };
}